import React, { useState } from 'react';
import styles from './member.module.css'
import put from '../../../axios/user/put';
import { useNavigate } from "react-router-dom";

const Member = () => {
    const navigate = useNavigate();

    const [user,setUser] = useState({
        name:'',
        password:''
    });

    const onchange = e =>{
        const {name, value} = e.target;
        setUser({
            ...user,
            [name]:value
        })
    }

    const userPost = (e) => {
        e.preventDefault(); 
        if(user.password === user.repassword)
        {
            if(put(user)){
                setUser({name:'', password:''});
                alert("   .");
                alert(" .");
                navigate("/login");
            }
            else{
                alert("  .");
            }           
        }else
            alert("  .");
    }
    
    return (
        <div className={styles.member}>
            <form onSubmit={userPost}>
                <div>
                    <label htmlFor="user-id"></label><br/>
                    <input type="text" name="name" onChange={(e)=>onchange(e)}/>
                </div>

                <div>
                    <label htmlFor="user-password"></label><br/>
                    <input name="password" type="password" onChange={(e)=>onchange(e)}/>
                </div>
                <div>
                    <label htmlFor="user-password-check"></label><br/>
                    <input name="repassword" type="password" onChange={(e)=>onchange(e)}/>
                </div>
                <div>
                    <input type="checkbox" name="user-term" />?
                </div>
                <div style={{marginTop:10}}>
                    <button type="submit"></button>
                </div>
            </form>
        </div>
    );
};

export default Member;