import React, { useEffect, useInsertionEffect } from 'react';
import styles from './study.module.css';
import { useSelector } from 'react-redux/es/exports';
import { useNavigate } from 'react-router-dom';
import { Link } from 'react-router-dom';
import { useState } from 'react';
import { OVGet } from '../../../axios/openvidu/get';
import study from '../../../img/study.png';

const Study = (props) => {
    const navigate = useNavigate();
    const { userId } = useSelector(state => ({
        userId:state.userId
    }));

    const [list,setList] = useState([{
        sessionModerator: "",
        sessionTitle: "",
        sessionId: "",
    }]);

    const onclick = (session, userName) => {
        if(localStorage.getItem('user-token') === null)
        {
            alert(" . .");
            navigate('/login');
        }
        else{
            navigate(`/webrtc?sessionId=${session}&userName=${userName}`);
        }
    }

    const edit = (sessionId) => {
        navigate(`/board/ovedit?sessionId=${sessionId}`);
    }

    useEffect(() => {
        async function fetchData(){
           setList(
            await OVGet()
           );
        }
        fetchData();
    },[props])

    return (
        <div className={styles.center}>
                <div className={styles.box}>
                    
                    <br/>
                    {list && list.map(list => <div key={list.sessionId} className={styles.poproom}><p onClick={()=>onclick(list.sessionId, userId)}>
                        <img style={styles.imagestyle} alt="study" src={study} />
                        <p className={styles.name}>{list.sessionTitle}</p></p>{list.sessionModerator===userId?<button className={styles.edit} onClick={()=>edit(list.sessionId)}>/</button>:<button className={styles.noedit}></button>}
                    </div>)}
                </div>
                <div className={styles.button}>
                    <br/>
                    <Link to={`/board/ovwrite`}><button></button></Link>
                </div>
        </div>
    );
};

export default Study;