import React from 'react';
import styles from './mypage.module.css'
import { useSelector } from 'react-redux';
import { useState } from 'react';
import { editInfo } from '../../../axios/userInfo/editInfo';
import { useNavigate } from 'react-router-dom';

const EditPage = () => {
    const navigate = useNavigate();
    const { userId } = useSelector(state => ({
        userId:state.userId
    }));
    const [user, setUser] = useState(
        {
            name:userId,
            password:""
        }
    );

    const edit = async(e) => {
        e.preventDefault();
        if (await editInfo(user)){
            alert(" .  ");
            localStorage.removeItem('user-token');
            navigate('/');
        }
        else
            alert(" .");
    }

    const onchange = e =>{
        const {name, value} = e.target;
        setUser({
            ...user,
            [name]:value
        })
    }

    return (
        <div className={styles.container}>
            <h1>  </h1>
            <div><label className={styles.name} htmlFor="cheese"> : </label><input className={styles.title_input} type='text' name="name" placeholder=' ' onChange={(e) => onchange(e)} value={user.name} readOnly/><br/>
            <label htmlFor="cheese">  : </label><input className={styles.title_input} type='password' name="password" placeholder=' ' onChange={(e) => onchange(e)} value={user.password}/><br/>
            <button onClick={e => edit(e)}></button></div>
        </div>
    );
};

export default EditPage;