import React from 'react';
import styles from './mypage.module.css'
import { useSelector } from 'react-redux';

const Mypage = () => {
    const { userId } = useSelector(state => ({
        userId:state.userId
    }));


    return (
        <div className={styles.container}>
            <h1> </h1>
            <div><label htmlFor="cheese">  : </label><input className={styles.title_input} type='text' name="postTitle" placeholder='' value={userId} readOnly/><br/></div>
        </div>
    );
};

export default Mypage;